
import {
	mapState,
	mapActions
} from 'vuex'
export default {
	computed: {
		...mapState([
			'globalData'
		])
	},
	onLaunch: function() {
		uni.getSystemInfo({
			success: e => {
				this.globalData.StatusBar = e.statusBarHeight;
				this.globalData.CustomBar = e.platform == 'android' ? e.statusBarHeight + 50 : e.statusBarHeight + 45;
			}
		})
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
